body {
  overflow: hidden;
}

html h1,
html h2,
html h3,
html h4,
html h5,
html h6,
html a,
html p,
html li,
input,
textarea,
span,
div,
html,
body,
html a {
  margin-bottom: 0;
  font-family: "muli-regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}

.setting-button {
  position: fixed;
  top: 50%;
  z-index: 3;
  right: 0;
  // background-color: #241d3b !important;
  box-shadow: none;
  font-size: 20px !important;
  padding: 10px !important;
}

.arrow-sidebar {
  top: 12px;
  position: absolute;
  right: 5px;
  font-size: 14px !important;
  z-index: 1;
  cursor: pointer;
}

.Page-title {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  .title-icon {
    width: 45px;
    height: 45px;
    font-weight: 600;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    border-radius: 6px;
  }
  .title {
    font-size: larger;
    font-weight: 600;
  }
}

.error-back {
  position: fixed;
  overflow: auto;
  top: 0;
  bottom: 0;
}

.error-container {
  box-shadow: 0 6px 6px -3px rgba(0, 0, 0, 0.2),
    0 10px 14px 1px rgba(0, 0, 0, 0.14), 0 4px 18px 3px rgba(0, 0, 0, 0.12) !important;
  text-align: center;
  padding: 20% 0;
  width: 100%;
  margin: 10% 0;
  .error-heading {
    font-size: 112px !important;
    font-weight: 300;
    line-height: 1 !important;
    letter-spacing: -0.04em !important;
    color: white;
  }
  .error-sub-title {
    font-size: 16px !important;
    font-weight: 400;
    color: white;
    padding: 20px 0;
  }
  .goHome {
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2),
      0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    background-color: #673ab7 !important;
    border-color: #673ab7 !important;
    color: white;
    padding: 9px 16px;
    border-radius: 30px;
    cursor: pointer;
    outline: 0;
  }
}

select.selectpicker {
  display: block;
  margin: 0 auto;
  padding-left: 20px;
}

.btn-new {
  background-color: #2a3f54;
}

.global-hash-title {
  font-size: 20px;
  font-weight: 600;
  margin: 30px 0;
}

.sweet-alert-confirm-button {
  color: #fff !important;
  background-color: #3f6ad8 !important;
  border-color: #3f6ad8 !important;
  padding: 10px 60px !important;
}

.sweet-alert-cancle-button {
  color: #fff !important;
  background-color: #d92550 !important;
  border-color: #d92550 !important;
  padding: 10px 60px !important;
}

.sweet-alert-wrapper {
  .lead {
    margin: 10px 0;
  }
  input {
    border-radius: 6px !important;
    font-size: 14px !important;
    border: 1px solid #ddd !important;
    &:focus {
      border: 1px solid #9a9a9a !important;
      box-shadow: none !important;
    }
  }
}

.icon-component {
  background: #eceff1;
  .icon {
    font-size: 36px;
    color: rgba(0, 0, 0, 0.54);
  }
  .text {
    color: rgba(0, 0, 0, 0.87);
    font-size: 14px;
  }
}

.profile-image {
  img {
    height: 100px;
    width: 100px;
    border-radius: 50%;
  }
}

.Profile-component {
  margin: -15px;
  .background {
    height: 250px;
  }
  .send-req-btn {
    position: absolute !important;
    top: 10px;
    right: 25px;
  }
  .profile-left-shade {
    position: relative;
    margin-top: -52px;
  }
  .profile-right-shade {
    background: white;
    position: relative;
    top: -25px;
  }
  .profile-list {
    font-weight: 600;
    font-size: 14px;
    padding-bottom: 10px;
    margin-right: 25px;
    cursor: pointer;
  }
  .feed-text-area {
    display: block;
    border-radius: 2px;
    min-width: 0;
    position: relative;
    text-decoration: none;
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
    box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
      0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
    background-color: #eee;
    border-color: #eee;
    padding: 16px;
    textarea {
      box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2),
        0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
      background: #fff;
      &:focus {
        border-color: white;
      }
    }
    .feed-text-area-icon {
      i {
        font-size: 20px;
        margin-right: 15px;
      }
    }
  }
  .feed-card {
    margin: 0 auto;
    padding: 15px;
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
    box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
      0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
    .feed-card-header {
      .feed-card-list-image {
        img {
          height: 40px;
          width: 40px;
          border-radius: 50%;
          margin-top: -26px;
        }
      }
      .feed-post-name {
        font-size: 15px;
        font-weight: bold;
      }
      .feed-post-time {
        font-size: 12px;
        font-weight: 300;
      }
    }
    .feed-card-body {
      .body-description {
        font-size: 14px;
        font-weight: 300;
      }
      .feed-card-media {
        img {
          width: 100%;
          height: 350px;
        }
      }
    }
  }
  .Work-header {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2),
      0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
    padding: 15px 15px;
    align-items: center;
  }
  .work-card {
    box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
      0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
    margin: 10px 0;
    .work-body {
      .Work-title {
        font-size: 15px;
        font-weight: 600;
      }
      .Work-text {
        font-size: 13px;
        font-weight: 300;
      }
    }
  }
  .see-all-btn {
    font-size: 12px;
    cursor: pointer;
  }
  .friend-image {
    width: 100%;
    object-fit: cover;
  }
  .profile-media-card {
    background-color: #f5f5f5 !important;
    padding: 25px;
    .media-image {
      height: 200px;
      width: 100%;
      object-fit: cover;
    }
    .media-text {
      position: absolute;
      top: 40%;
      text-align: center;
      width: 88%;
      color: white;
      font-weight: bold;
    }
  }
}

