  line-height: 1.5 !important;
}

.c-btn-lg.c-btn-wide,
.c-btn-group-lg > .c-btn-wide.c-btn {
  padding: 0.5rem 2rem !important;
  font-size: 1.1rem !important;
  line-height: 1.5 !important;
